//== DarkMode ==//
body {
    --bkg-color: #fff;
    --text-color: #222;
	--text-color-blue: Blue;
	--border-color: #ddd;
	--selection-text-color: black;
	--selection-bkg-color: #707070;
}
body.dark-theme {
	--bkg-color: #121212;
    --bkg-color-2: black;
    --text-color: #eee;
	--text-color-blue: #0078ff;
	--border-color: #333;
	--selection-text-color: #333;
	--selection-bkg-color: #111;
}
@media (prefers-color-scheme: dark) {
    body {
        --bkg-color: #121212;
        --bkg-color-2: black;
        --text-color: #eee;
		--text-color-blue: #0078ff;
		--border-color: #333;
		--selection-text-color: #333;
		--selection-bkg-color: #111;
    }
    body.light-theme {
        --bkg-color: #fff;
        --bkg-color-2: white;
        --text-color: #222;
		--text-color-blue: Blue;
		--border-color: #ddd;
		--selection-text-color: black;
		--selection-bkg-color: #707070;
    }
}

